}

.user__dashboard {
	padding: 0rem 1rem;
}

.user__dashboard__header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 110px;
}

.user__dashboard__header .client_img {
	box-shadow: 1px 2px 6px #e1e1ef;
}

.user__dashboard__header img {
	background: #fff;
	border-radius: 6px;
	padding: 0.5rem;
	height: 70px;
	width: 70px;
}

.user__dashboard__header .assignment_right {
	display: flex;
	align-items: center;
	grid-gap: 1rem;
	gap: 1rem;
}

.user__dashboard__header button {
	background: #ffc65a;
	height: 46px;
	width: 46px;
	border-radius: 6px;
	border: none;
	cursor: pointer;
}

.user__dashboard__header button img {
	background: #ffc65a;
	height: 46px;
	width: 46px;
}

.user__dashboard__body__inner {
	--bs-gutter-x: 1rem;
	--bs-gutter-y: 1rem;
	display: flex;
	flex-wrap: wrap;
	margin-top: calc(var(--bs-gutter-y) * -1);
	margin-right: calc(var(--bs-gutter-x) * -0.5);
	margin-left: calc(var(--bs-gutter-x) * -0.5);
	height: calc(100vh - 110px);
	overflow-y: auto;
	padding-bottom: 1rem;
}

.user__dashboard__body__inner::-webkit-scrollbar {
	width: 7px;
}
.user__dashboard__body__inner::-webkit-scrollbar-track {
	background: #e2e6eb;
}
.user__dashboard__body__inner::-webkit-scrollbar-thumb {
	background: #1c395c;
	border-radius: 10px;
}

.user__dashboard__body__inner > * {
	flex-shrink: 0;
	width: 100%;
	max-width: 100%;
	padding-right: calc(var(--bs-gutter-x) * 0.5);
	padding-left: calc(var(--bs-gutter-x) * 0.5);
	margin-top: var(--bs-gutter-y);
}

